<template>
  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-col :span="3">
        <el-switch
          width= "100"
          v-model="userType"
          active-value="1"
          inactive-value="0"
          on-text="全部用户"
          off-text="真实用户"
          @change="reSearch()"
        >
        </el-switch>
      </el-col>
      <el-col :span="6">
        <router-link :to="{ path: '/tongji/12'}">
          <el-button type="primary" >用户呼明细</el-button>
        </router-link>
        <router-link :to="{ path: '/tongji/13'}">
          <el-button type="primary" >用户呼叫汇总</el-button>
        </router-link>
      </el-col>
      <el-col :span="12">
        <el-date-picker
          v-model="searchKey"
          type="datetimerange"
          align="right"
          unlink-panels
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          :picker-options="pickerOptions2"
          @change="search($event)">
        </el-date-picker>
      </el-col>
    </h3>
    <div slot="body">
      <el-table
        :data="tableData.rows"
        border
        style="width: 100%"
        v-loading="listLoading"
        @selection-change="handleSelectionChange"
        @cell-click="gonext"
       >
        <!--checkbox 适当加宽，否则IE下面有省略号 https://github.com/ElemeFE/element/issues/1563-->
        <el-table-column
          prop="id"
          type="selection"
          width="50">
        </el-table-column>
        <el-table-column
          prop="caller_tel"
          label="用户手机号"
         >
        </el-table-column>
        <el-table-column
          prop="caller_name"
          label="用户名称"
         >
        </el-table-column>
        <el-table-column
          prop="callnum"
          label="呼叫次数"
         >
        </el-table-column>
        <el-table-column
          prop="callfailnum"
          label="呼叫失败数"
         >
        </el-table-column>
        <el-table-column
          prop="chatfailnum"
          label="未接通数"
         >
        </el-table-column>
        <el-table-column
          prop="chatnum"
          label="已接通数"
         >
        </el-table-column>
        <el-table-column
          prop="recallnum"
          label="重复数量"
        >
        </el-table-column>
        <el-table-column
          prop="totaltime"
          label="总时长"
        >
        </el-table-column>
        <el-table-column
          prop="avgtime"
          label="平均时长"
        >
        </el-table-column>
        <el-table-column
          prop="chatrate"
          label="接通率"
        >
        </el-table-column>
        <el-table-column
          prop=""
          label="时间段"
        >
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="tableData.pagination.pageNo"
        :page-sizes="[5, 10, 20]"
        :page-size="tableData.pagination.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.pagination.total">
      </el-pagination>
      <div id="myChart" :style="{width: '1000px', height: '300px'}"></div>
    </div>
  </imp-panel>
</template>

<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"
  import testData from "../../../static/data/data.json"
  import * as tongjiApi from '../../services/tongji'

  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        userType: Boolean(this.$route.query.userType),
        currentRow: {},
        dialogVisible: false,
        dialogLoading: false,
        defaultProps: {
          children: 'children',
          label: 'name',
          id: "id",
        },
        roleTree: [],
        listLoading: false,
        tableData: {
          pagination: {
            total: 0,
            pageNo: 1,
            pageSize: 10,
            parentId: 0
          },
          rows: []
        },
       // pickerOptions2: {
       //   shortcuts: [{
       //     text: '最近15天',
       //       onClick(picker) {
       //       const end = new Date();
       //       const start = new Date();
       //       start.setTime(start.getTime() - 3600 * 1000 * 24 * 14);
       //       picker.$emit('pick', [start, end]);
       //     }
       //   },{
       //     text: '最近30天',
       //     onClick(picker) {
       //       const end = new Date();
       //       const start = new Date();
       //       start.setTime(start.getTime() - 3600 * 1000 * 24 * 29);
       //       picker.$emit('pick', [start, end]);
       //     }
       //   }]
       // },
        searchKey:[this.$moment(this.$route.query.date).format('YYYY-MM-DD 00:00:00'),this.$moment(this.$route.query.date).add(1,'days').format('YYYY-MM-DD 00:00:00')],
      }
    },
    methods: {
      search(target){
        this.loadData();
      },
      reSearch(){
        this.loadData();
      },
      handleSelectionChange(val){

      },
      handleSizeChange(val) {
        this.tableData.pagination.pageSize = val;
        this.loadData();
      },
      handleCurrentChange(val) {
        this.tableData.pagination.pageNo = val;
        this.loadData();
      },
      gonext(row, val, cell, event){
        this.$router.push({path: '/tongji/12',query:{tel:row.caller_tel,date:this.$route.query.date,userType:this.userType}});
      },
      loadData(){
          tongjiApi.userActivity({
            key: this.searchKey,
            userType: this.userType
          })
          .then(res => {
            this.tableData.rows = res.callUser;
            //x轴数据
            let callUserNum = res.callUserNum[0].callUserNum;
            let callSuccessUserNum = res.callSuccessUserNum[0].callSuccessUserNum;
            let chatUserNum = res.chatUserNum[0].chatUserNum;
            let chatSuccessUserNum = res.chatSuccessUserNum[0].chatSuccessUserNum;
    
            //初始化
            let myChart = this.$echarts.init(document.getElementById('myChart'));
            // 绘制图表
            myChart.setOption({
                title: {
                    text: '用户活跃度'
                },
                toolbox: {
                    feature: {
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: 'category',
                    data: ['呼叫用户总数','呼叫成功用户数','接通用户数','接通成功用户数']
                },
                yAxis: {
                    type: 'value'
                },
                series: [
                  {
                    data: [callUserNum,callSuccessUserNum,chatUserNum,chatSuccessUserNum],
                    type: 'line'
                  }
                ]
            });   
          });
      },

    },
    created(){
      this.loadData();
    },

  }
</script>
<style>
  .el-pagination {
    float: right;
    margin-top: 15px;
  }
</style>



