<template>
  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-row style="width: 100%;">
        <el-col :span="3">
          <el-button type="primary" icon="download" @click="download()">导出excel</el-button>
        </el-col>
        <el-col :span="5">
            <el-select v-model="cycle" placeholder="请选择"  @change="unitChange($event)">
            <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
           </el-select>
        </el-col>
        <el-col :span="10">
            <el-date-picker
              v-model="searchKey" 
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              value-format="yyyy-MM-dd HH:mm:ss"
              @change="search($event)">
            </el-date-picker>
        </el-col>
        <el-col :span="4" style="text-align:left">
            <el-switch
                width= "100"
                v-model="userType"
                active-value="1"
                inactive-value="0"
                on-text="全部用户"
                off-text="真实用户"
                @change="reSearch()">
            </el-switch>
        </el-col>
      </el-row>
    </h3>
    <div slot="body">
      <el-table
        :data="tableData.rows"
        border
        style="width: 100%"
        v-loading="listLoading"
        @cell-click="gonext"
        @selection-change="handleSelectionChange">
        <!--checkbox 适当加宽，否则IE下面有省略号 https://github.com/ElemeFE/element/issues/1563-->
        <el-table-column
          prop="hangup_reason"
          type="selection"
          width="50">
        </el-table-column>
        <el-table-column
          prop="total"
          label="总数" 
          >
        </el-table-column>
        <el-table-column
          prop="succ"
          label="成功" 
          >
        </el-table-column>
        <el-table-column
          prop="fail"
          label="失败" 
          >
        </el-table-column>
        <el-table-column
          prop="date"
          label="时间"
        >
        </el-table-column>
      </el-table>
    </div>
  </imp-panel>
</template>

<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"
  import testData from "../../../static/data/data.json"
  import * as tongjiApi from '../../services/tongji'

  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        cycle: 'day',
        options: [{
          value: 'day',
          label: '按日统计'
        }, {
          value: 'week',
          label: '按周统计'
        }, {
          value: 'month',
          label: '按月统计'
        }],
        userType: false,
        currentRow: {},
        dialogVisible: false,
        dialogLoading: false,
        defaultProps: {
          children: 'children',
          label: 'name',
          id: "id",
        },
        roleTree: [],
        listLoading: false,
        searchKey: [this.$moment(Date.now()).subtract(10,'days').format('YYYY-MM-DD'),this.$moment(Date.now()).format('YYYY-MM-DD')],
        searchReason: '所有原因',
        tableData: {
          pagination: {
            total: 0,
            pageNo: 1,
            pageSize: 10,
            parentId: 0
          },
          rows: []
        }
      }
    },
    methods: {
      unitChange(){
        this.loadData();
      },
      search(target){
        this.loadData();
      },
      reSearch() {
        this.loadData();
      },
      reCount() {
        this.loadData();
      },
      handleSizeChange(val) {
        this.tableData.pagination.pageSize = val;
        this.loadData();
      },
      handleCurrentChange(val) {
        this.tableData.pagination.pageNo = val;
        this.loadData();
      },
       gonext(row, val, cell, event) {
        if ( val.label == "总数") {
          this.$router.push({path: '/tongji/selfCheckDetail',query:{timeType:this.cycle,type:'all',date:row.date,userType:this.userType}});
        }
        if ( val.label == "成功") {
            this.$router.push({path: '/tongji/selfCheckDetail',query:{timeType:this.cycle,type:'success',date:row.date,userType:this.userType}});
        }
        if ( val.label == "失败") {
            this.$router.push({path: '/tongji/selfCheckFailReason',query:{timeType:this.cycle,total:row.total,fail:row.fail2,date:row.date,userType:this.userType}});

        }
      },
      download(){
        tongjiApi.CountCallFailReasonExcel({
          searchKey: this.searchKey
        })
        .then( res => {
          if ( res.code == "200" || res.code == 200 ) {
              window.open("http://101.132.132.117:12580/"+res.data,"_self");
            }
        });
      },
      loadData(){
          tongjiApi.selfCheck({
            key: this.searchKey,
            timeType: this.cycle,
            userType: this.userType,
            searchReason: this.searchReason,
          })
          .then(res => {
            this.tableData.rows = res;
          });
      }
    },
    created(){
      this.loadData();
    }
  }
</script>
<style>
  .el-pagination {
    float: right;
    margin-top: 15px;
  }
</style>



