<template>
  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-row>
        <el-col :span="3">
          <el-dropdown split-button type="primary"  @click="handleClick" @command="handleCommand">
            {{cycle}}
            <el-dropdown-menu slot="dropdown"  >
              <el-dropdown-item command="day">按日统计</el-dropdown-item>
              <el-dropdown-item command="week">按周统计</el-dropdown-item>
              <el-dropdown-item command="month">按月统计</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-col>
        <el-col :span="3">
          <el-switch
            width= "100"
            v-model="userType"
            active-value="1"
            inactive-value="0"
            on-text="全部用户"
            off-text="真实用户"
            @change="reSearch()"
          >
          </el-switch>
        </el-col>
        <el-col :span="12">
          <div class="chooseDate" style="float:right;">
            <el-date-picker
              v-model="searchKey"
              type="daterange"
              align="right"
              unlink-panels
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :picker-options="pickerOptions2"
              @change="search($event)">
            </el-date-picker>
          </div>
        </el-col>  
      </el-row>
    </h3>
    <div slot="body">
        <el-table
        :data="tableData.rows"
        border
        style="width: 100%"
        v-loading="listLoading"
        @selection-change="handleSelectionChange"
        @cell-click="gonext"
        @header-click="selectTongJi">
        <!--checkbox 适当加宽，否则IE下面有省略号 https://github.com/ElemeFE/element/issues/1563-->
        <el-table-column
          prop="id"
          type="selection"
          width="50">
        </el-table-column>
        <el-table-column
          prop="usernum"
          label="呼叫用户" 
          width="100"
        >
        </el-table-column>
        <el-table-column
          prop="callnum"
          label="呼叫数"
         >
        </el-table-column>
        <el-table-column
          prop="callsuccessnum2"
          label="呼叫成功"
         >
        </el-table-column>
        <el-table-column
          prop="callfailnum2"
          label="呼叫失败"
         >
        </el-table-column>
        <el-table-column
          prop="answersuccessnum2"
          label="接听数"
         >
        </el-table-column>
        <el-table-column
          prop="answerfailnum2"
          label="未接听数"
         >
        </el-table-column>
        <el-table-column
          prop="chatsuccessnum2"
          label="接通数"
         >
        </el-table-column>
        <el-table-column
          prop="chatfailnum2"
          label="未接通数"
         >
        </el-table-column>
        <el-table-column
          prop="auviyes2"
          label="音视频正常"
         >
        </el-table-column>
        <el-table-column
          prop="auvino2"
          label="音视频异常"
         >
        </el-table-column>
             <el-table-column
          prop="duration"
          label="通话时长"
         >
        </el-table-column>
        <el-table-column
          prop="createdAt"
          label="统计日期"
         >
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="tableData.pagination.pageNo"
        :page-sizes="[50,100,150]"
        :page-size="tableData.pagination.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.pagination.total">
      </el-pagination>
      <div class="description"><p>备注：点表头的单元格下面统计图会显示相应的数据，点表格中的数据单元格可以查看相应的数据明细。呼叫成功指服务器收到呼叫请求，接听指亲友或客服点击接听按钮，接通指连接通话服务器正常，音视频正常指音视频数据完全正常</p></div>
      <div id="myChart" :style="{width: '100%', height: '600px'}"></div>
    </div>
  </imp-panel>
</template>

<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"
  import testData from "../../../static/data/data.json"
  import * as tongjiApi from '../../services/tongji'

  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        cycle: '按日统计',
        command: 'day',
        userType:true,
        showDate: '',
        showRate: '呼叫数', 
        showCol: '1',
        currentRow: {},
        myChart: {},
        dialogVisible: false,
        dialogLoading: false,
        defaultProps: {
          children: 'children',
          label: 'name',
          id: "id",
        },
        roleTree: [],
        listLoading: false,
        searchKey: [new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 10), new Date()],
        tableData: {
          pagination: {
            total: 0,
            pageNo: 1,
            pageSize: 10,
            parentId: 0
          },
          rows: []
        },
        pickerOptions2: {
          shortcuts: [
          {
            text: '最近7天',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近15天',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 15);
              picker.$emit('pick', [start, end]);
            }
          },{
            text: '最近30天',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
      }
    },
    methods: {
      handleSelectionChange(val){
      },
      handleSizeChange(val) {
        this.tableData.pagination.pageSize = val;
        this.loadData();
      },
      handleCurrentChange(val) {
        this.tableData.pagination.pageNo = val;
        this.loadData();
      },
      reSearch() {
        this.loadData();
      },
      search(target){
        this.loadData();
      },
      gonext(row, val, cell, event) {
        if ( val.label == "呼叫用户") {
          this.$router.push({path: '/tongji/userActivity',query:{date:row.createdAt,userType:this.userType}});
        }
        if ( val.label == "呼叫数") {
          this.$router.push({path: '/tongji/7',query:{date:row.createdAt,userType:this.userType}});
        }
        if ( val.label == "呼叫成功") {
          this.$router.push({path: '/tongji/7',query:{date:row.createdAt,type:'success',userType:this.userType}})
        }
        if ( val.label == "呼叫失败") {
           this.$router.push({path: '/tongji/16',query:{date:row.createdAt,userType:this.userType}});
        }
        if ( val.label == "接听数") {
          this.$router.push({path: '/tongji/countAnswerDetail',query:{date:row.createdAt,total:row.answersuccessnum,userType:this.userType}});
        }
        if ( val.label == "未接听数") {
          this.$router.push({path: '/tongji/5',query: {date:row.createdAt,userType:this.userType}});
        }
        if ( val.label == "接通数") {
          this.$router.push({path: '/tongji/8',query:{date:row.createdAt,total:row.chatsuccessnum,userType:this.userType}});
        }
        if ( val.label == "未接通数") {
          this.$router.push({path: '/tongji/countChatFailDetail',query:{date:row.createdAt,total:row.chatfailnum,userType:this.userType}});        
        }
        if ( val.label == "音视频正常") {
          this.$router.push({path: '/tongji/8',query:{date:row.createdAt,total:row.auviyes,type:'success',userType:this.userType}});          
        }
        if ( val.label == "音视频异常") {
          this.$router.push({path: '/tongji/6',query:{userType:this.userType}});
        }	
      },
      selectTongJi(val) {
        if ( val.label == "呼叫用户") {
          this.showCol = 1;
          this.loadData();
        }
        if ( val.label == "呼叫数") {
          this.showCol = 2;
          this.loadData();
        }
        if ( val.label == "呼叫成功") {
          this.showCol = 3;
          this.loadData();
        }
        if ( val.label == "呼叫失败") {
          this.showCol = 4;
          this.loadData(); 
        }
        if ( val.label == "接听数") {
          this.showCol = 5;
          this.loadData();
        }
        if ( val.label == "未接听数") {
          this.showCol = 6;
          this.loadData();
        }
        if ( val.label == "接通数") {
          this.showCol = 7;
          this.loadData();
        }
        if ( val.label == "未接通数") {
          this.showCol = 8;
          this.loadData();
        }
        if ( val.label == "音视频正常") {
          this.showCol = 9;
          this.loadData();
        }
        if ( val.label == "音视频异常") {
          this.showCol= 10;
          this.loadData();
        }	
      },
      handleCommand(command) {
        if( command == "day") {
          this.cycle="按日统计";
        };
        if( command == "week") {
          this.cycle="按周统计";
        };
        if( command == "month") {
          this.cycle="按月统计";
        };
        this.command = command;
        this.loadData();
      },
      loadData(){
          tongjiApi.CountCallanswerchat({
            key: this.searchKey,
            userType: this.userType,
            type: this.command,
            pageSize: this.tableData.pagination.pageSize,
            pageNo: this.tableData.pagination.pageNo
          })
          .then(res => {
            const e = this;
            this.tableData.rows = res.records;
            this.tableData.pagination.total = res.total;
            //x轴数据
            let dx1 = this.$moment(this.searchKey[0]).format('YYYY-MM-DD');
            let dx2 = this.$moment(this.searchKey[1]).format('YYYY-MM-DD');
            let days = (this.searchKey[1]-this.searchKey[0])/(24*60*60*1000);
            let dx = [];
            let userNum = [];
            let callNum = [];
            let callSuccessNum = [];
            let callSuccessRate = [];
            let callFailNum = [];
            let callFailRate = [];
            let answerNum = [];
            let answerNumRate2 = [];
            let answerFailNum = [];
            let answerFailNumRate2 = [];
            let chatNum = [];
            let chatNumRate2 = []; 
            let chatFailNum = [];
            let chatFailNumRate2 = [];
            let auviyes = [];
            let auviyesRate2 = [];
            let auvino = [];
            let auvinoRate2 = [];

            let vo = [];
            for ( var i=0; i<=days ;i++ )  {
              let d = this.$moment(dx1).add(i, 'days').format('YYYY-MM-DD');
              dx.push(d);
            }
            for ( let index in res.records ) {
              let x = dx.indexOf(res.records[index].createdAt);
              if ( x != -1 ) {
                userNum[x] = parseInt(res.records[index].usernum);
                callNum[x] = parseInt(res.records[index].callnum);
                callSuccessNum[x] = parseInt(res.records[index].callsuccessnum);
                callSuccessRate[x] = parseInt(res.records[index].callSuccessRate);
                callFailNum[x] = parseInt(res.records[index].callfailnum);
                callFailRate[x] = parseInt(res.records[index].callFailRate);

                answerNum[x] = parseInt(res.records[index].answersuccessnum);
                answerNumRate2[x] = parseInt(res.records[index].answerSuccessRate2);

                answerFailNum[x] = parseInt(res.records[index].answerfailnum);
                answerFailNumRate2[x] = parseInt(res.records[index].answerFailRate2);

                chatNum[x] = parseInt(res.records[index].chatsuccessnum);
                chatNumRate2[x] = parseInt(res.records[index].chatSuccessRate2);

                chatFailNum[x] = parseInt(res.records[index].chatfailnum);
                chatFailNumRate2[x] = parseInt(res.records[index].chatFailRate2);

                auviyes[x] = parseInt(res.records[index].auviyes);
                auviyesRate2[x] = parseInt(res.records[index].auviyesRate2);

                auvino[x] = parseInt(res.records[index].auvino);
                auvinoRate2[x] = parseInt(res.records[index].auvinoRate2);

              }
            }
            console.log()
            //初始化
            this.myChart = this.$echarts.init(document.getElementById('myChart'));
            if (this.showCol == 1) {
              var option = {
                    '呼叫用户': true,
                    '呼叫数': false,
                    '呼叫成功数': false,
                    '呼叫成功数率': false,
                    '呼叫失败数': false,
                    '呼叫失败率': false,
                    '接听数': false,
                    '接听成功率': false,
                    '未接听数': false,
                    '接听失败率': false,
                    '接通数': false,
                    '接通成功率': false,
                    '未接通数': false,
                    '接通失败率': false,
                    '音视频正常': false,
                    '音视频正常率': false,
                    '音视频异常': false,
                    '音视频异常率': false
              };
              var legendData = ['呼叫用户'];
            };
            if (this.showCol == 2) {
              var option = {
                    '呼叫用户': false,
                    '呼叫数': true,
                    '呼叫成功数': false,
                    '呼叫成功数率': false,
                    '呼叫失败数': false,
                    '呼叫失败率': false,
                    '接听数': false,
                    '接听成功率': false,
                    '未接听数': false,
                    '接听失败率': false,
                    '接通数': false,
                    '接通成功率': false,
                    '未接通数': false,
                    '接通失败率': false,
                    '音视频正常': false,
                    '音视频正常率': false,
                    '音视频异常': false,
                    '音视频异常率': false
              };
              var legendData = ['呼叫数'];
            };
            if (this.showCol == 3) {
              var option = {
                    '呼叫用户': false,
                    '呼叫数': false,
                    '呼叫成功数': true,
