                    '音视频正常率': false,
                    '音视频异常': false,
                    '音视频异常率': false
              };
              var legendData = ['未接听数','接听失败率'];
            };
            if (this.showCol == 7) {
              var option = {
                    '呼叫用户': false,
                    '呼叫数': false,
                    '呼叫成功数': false,
                    '呼叫成功数率': false,
                    '呼叫失败数': false,
                    '呼叫失败率': false,
                    '接听数': false,
                    '接听成功率': false,
                    '未接听数': false,
                    '接听失败率': false,
                    '接通数': true,
                    '接通成功率': true,
                    '未接通数': false,
                    '接通失败率': false,
                    '音视频正常': false,
                    '音视频正常率': false,
                    '音视频异常': false,
                    '音视频异常率': false
              };
              var legendData = ['接通数','接通成功率'];              
            };
            if (this.showCol == 8) {
              var option = {
                    '呼叫用户': false,
                    '呼叫数': false,
                    '呼叫成功数': false,
                    '呼叫成功数率': false,
                    '呼叫失败数': false,
                    '呼叫失败数': false,
                    '接听数': false,
                    '接听成功率': false,
                    '未接听数': false,
                    '接听失败率': false,
                    '接通数': false,
                    '接通成功率': false,
                    '未接通数': true,
                    '接通失败率': true,
