                    '音视频正常率': false,
                    '音视频异常': false,
                    '音视频异常率': false
              };
              var legendData = ['未接通数','接通失败率'];              
            };
            if (this.showCol == 9) {
              var option = {
                    '呼叫用户': false,
                    '呼叫数': false,
                    '呼叫成功数': false,
                    '呼叫成功数率': false,
                    '呼叫失败数': false,
                    '呼叫失败率': false,
                    '接听数': false,
                    '接听成功率': false,
                    '未接听数': false,
                    '接听失败率': false,
                    '接通数': false,
                    '接通成功率': false,
                    '未接通数': false,
                    '接通失败率': false,
                    '音视频正常': true,
                    '音视频正常率': true,
                    '音视频异常': false,
                    '音视频异常率': false
              };
              var legendData = ['音视频正常','音视频正常率'];              
            };
            if (this.showCol == 10) {
              var option = {
                    '呼叫用户': false,
                    '呼叫数': false,
                    '呼叫成功数': false,
                    '呼叫成功数率': false,
                    '呼叫失败数': false,
                    '呼叫失败率': false,
                    '接听数': false,
                    '接听成功率': false,
                    '未接听数': false,
                    '接听失败率': false,
                    '接通数': false,
                    '接通成功率': false,
                    '未接通数': false,
                    '接通失败率': false,
                    '音视频正常': false,
                    '音视频正常率': false,
                    '音视频异常': true,
                    '音视频异常率': true
              };
              var legendData = ['音视频异常','音视频异常率'];              
            };
            // 绘制图表
            this.myChart.setOption({
              tooltip: {
                  trigger: 'axis'
              },
              legend: {
                  data: legendData,
                  selected: option,
              },
              grid: {
                  left: '3%',
                  right: '4%',
                  bottom: '3%',
                  containLabel: true
              },
              toolbox: {
                  feature: {
                      saveAsImage: {}
                  }
              },
              xAxis: {
                  type: 'category',
                  boundaryGap: false,
                  data: dx
              },
              yAxis: [
                {
                  type: 'value',
                  name: '数量'
                },
                {
                  type: 'value',
                  name: '概率',
                  axisLabel: {
                    formatter: '{value} %'
                  }
                }
              ],
              series: [
                 {
