                  },
                   {
                      name:'接听成功率',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '数量55',
                      data:answerNumRate2
                  },
                  {
                      name:'未接听数',
                      type:'line',
                      stack: '数量6',
                      data:answerFailNum
                  },
                   {
                      name:'接听失败率',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '数量66',
                      data:answerFailNumRate2
                  },
                  {
                      name:'接通数',
                      type:'line',
                      stack: '数量7',
                      data:chatNum
                  },
                  {
                      name:'接通成功率',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '数量77',
                      data:chatNumRate2
                  },
                  {
                      name:'未接通数',
                      type:'line',
                      stack: '数量8',
                      data:chatFailNum
                  },
                  {
                      name:'接通失败率',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '数量88',
                      data:chatFailNumRate2
                  },
                   {
                      name:'音视频正常',
                      type:'line',
                      stack: '数量9',
                      data:auviyes
                  },
                  {
                      name:'音视频正常率',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '数量99',
                      data:auviyesRate2
                  },
                  {
                      name:'音视频异常',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '数量10',
                      data:auvino
                  },
                  {
                      name:'音视频异常率',
                      type:'line',
                      yAxisIndex: 1,
                      stack: '数量11',
                      data:auvinoRate2
                  }
              ]
            });
            var go = this.$router;
            //绑定点击事件
            myChart.on('click', function (params) {
              alert(params.seriesName);
              let date = params.name;
              let value = params.value;
              if ( params.seriesName == '呼叫用户' ) {
                go.push({path: '/tongji/userActivity'});
              }
              if ( params.seriesName =='呼叫数') {
                go.push({path: '/tongji/7',query:{dateKey:date}});
              }
              if ( params.seriesName == '呼叫成功' ) {
                go.push({path: ''});
              }
              if ( params.seriesName == '呼叫失败' ) {
                go.push({path: '/tongji/16'});
              }
              if ( params.seriesName == '接听数' ) {
                go.push({path: ''});
              }
              if ( params.seriesName == '未接听数' ) {
                go.push({path: '/tongji/5'});
              }
              if ( params.seriesName == '接通数' ) {
                go.push({path: '/tongji/8'});
              }
              if ( params.seriesName == '未接通数' ) {
                go.push({path: '/tongji/8'});
              }
              if ( params.seriesName == '音视频正常' ) {
                go.push({path: '/tongji/6'});
              }
              if ( params.seriesName == '音视频异常' ) {
                go.push({path: '/tongji/6'});
              }
              
            });
          });
      }
    },
    created(){
      this.loadData();
    }
  }
</script>
<style>
  .el-pagination {
    float: right;
    margin-top: 15px;
  }
  #myChart {
    margin-top:50px;
  }
  .description{
   margin-top: 60px;
   font-size:12px;
   color:#333;
  }
</style>



