<template>
  <imp-panel>
    <div slot="body">
        <el-row :gutter="12">
            <el-col :span="6">
                <el-card shadow="always" @click.native="goNext('/tongji/1')">
                  用户数
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card shadow="always" @click.native="goNext('/tongji/4')">
                  呼叫接听接通数汇总
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card shadow="always" @click.native="goNext('/tongji/10')">
                视友基本信息
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card shadow="hover" @click.native="goNext('/tongji/11')">
                    亲友基本信息
                </el-card>
            </el-col>
        </el-row>
        <el-row :gutter="12">
          <el-col :span="6">
            <el-card shadow="always" @click.native="goNext('/tongji/ucacDetail')">
              基础数据采集
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card shadow="hover" @click.native="goNext('/tongji/selfCheck')">
              一键自检
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card shadow="hover" @click.native="goNext('/tongji/cusAnswer')">
              客服接听情况
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card shadow="hover" @click.native="goNext('/tongji/userCallChat')">
              用户活跃度
            </el-card>
          </el-col>
        </el-row>
        <el-row :gutter="12">
          <el-col :span="6">
            <el-card shadow="always" @click.native="goNext('/tongji/chatTime')">
              通话情况
            </el-card>
          </el-col>
        </el-row>
    </div>
  </imp-panel>
</template>

<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"
  import testData from "../../../static/data/data.json"
  import * as businessApi from '../../services/business'
  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        form: {
          id: '',
          tel: '',
          password: '',
        },
        currentRow: {},
        dialogVisible: false,
        dialogLoading: false,
        defaultProps: {
          children: 'children',
          label: 'rolename',
          id: "id",
        },
        roleTree: [],
        listLoading: false,
        searchKey: '',
        tableData: {
          pagination: {
            total: 0,
            pageNo: 1,
            pageSize: 10,
            parentId: 0
          },
          rows: []
        }
      }
    },
    methods: {
      goNext(link) {
        this.$router.push({path: link})
      }
    } 
  }
</script>
<style>
  .el-card {
    margin-bottom: 10px;
  }
</style>



