<template>
  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-row style="width: 100%;">
        <el-col :span="6">
           <el-select v-model="cycle" placeholder="请选择"  @change="unitChange($event)">
            <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
           </el-select>
        </el-col>
        <el-col :span="6">
            <el-date-picker
                v-model="searchKey"
                :type="datetype"
                :format="dateformat"
                :placeholder="datedesc"
                @change="search($event)"
            >
            </el-date-picker>
        </el-col>
        <el-col :span="6">
            <el-radio-group v-model="demos" @change="changeDemos($event)">
                <el-radio :label="0">呼通次数</el-radio>
                <el-radio :label="1">通话时长</el-radio>
            </el-radio-group>
        </el-col>
      </el-row>
     

    </h3>
    <div slot="body">
      <div id="myChart" :style="{width: '100%', height: '600px'}"></div>
      <el-table
        :data="tableData.rows"
        style="width: 100%"
        max-height="800"
        v-loading="listLoading"
        @cell-click="gonext"
        @selection-change="handleSelectionChange"
        v-if="this.demos">
        <!--checkbox 适当加宽，否则IE下面有省略号 https://github.com/ElemeFE/element/issues/1563-->
        <el-table-column
          prop="caller_uid"
          label="用户id" 
          >
        </el-table-column>
        <el-table-column
          prop="caller_tel"
          label="手机号"
         >
        </el-table-column>
        <el-table-column
          prop="durations"
          label="时长"
        >
        </el-table-column>
        <el-table-column
          prop="last_call_time"
          label="最后一次呼叫时间"
        >
        </el-table-column>
      </el-table>
      <el-table
        :data="tableData.rows"
        style="width: 100%"
        max-height="800"
        v-loading="listLoading"
        @cell-click="gonext"
        @selection-change="handleSelectionChange"
        v-else>
        <!--checkbox 适当加宽，否则IE下面有省略号 https://github.com/ElemeFE/element/issues/1563-->
        <el-table-column
          prop="caller_uid"
          label="用户id" 
          >
        </el-table-column>
        <el-table-column
          prop="caller_tel"
          label="手机号"
         >
        </el-table-column>
        <el-table-column
          prop="chatnum"
          label="次数"
        >
        </el-table-column>
        <el-table-column
          prop="last_call_time"
          label="最后一次呼叫时间"
        >
        </el-table-column>
      </el-table>
      
    </div>
  </imp-panel>
</template>

<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"
  import testData from "../../../static/data/data.json"
  import * as tongjiApi from '../../services/tongji'
  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        cycle: 'day',
        options: [{
          value: 'day',
          label: '按日统计'
        }, {
          value: 'week',
          label: '按周统计'
        }, {
          value: 'month',
          label: '按月统计'
        }],
        datetype: 'daterange',
        datedesc: '选择日期',
        dateformat: '',
        linkType: '',
        currentRow: {},
        dialogVisible: false,
        dialogLoading: false,
        defaultProps: {
          children: 'children',
          label: 'name',
          id: "id",
        },
        roleTree: [],
        listLoading: false,
        searchKey: [this.$moment(Date.now()-24 * 60 * 60 * 1000*7).format('YYYY-MM-DD'),this.$moment(Date.now()).format('YYYY-MM-DD')],
        searchtel: '',
        demos: 0,
        tableData: {
          pagination: {
            total: 0,
            pageNo: 1,
            pageSize: 10,
            parentId: 0
          },
          rows: []
        },
      }
    },
    methods: {
      unitChange(val) {
        if( val == "day") {
          this.cycle="day";
          this.datetype = "daterange";
          this.datedesc = "选择日期";
          this.dateformat = "";
          this.searchKey = [this.$moment(Date.now()-24 * 60 * 60 * 1000*7).format('YYYY-MM-DD'),this.$moment(Date.now()).format('YYYY-MM-DD')];
        };
        if( val == "week") {
          this.cycle="week";
          this.datetype = "week";
          this.searchKey = '';
          this.datedesc = "选择周";
          this.dateformat = "yyyy 第 WW 周";
        };
        if( val == "month") {
          this.cycle="month";
          this.datetype = "month";
          this.searchKey = '';
          this.datedesc = "选择月";
          this.dateformat = '';
        };
        this.loadData();
      },
      changeDemos(val) {
          this.loadData();
      },
      search(target){
        this.loadData();
      },
      handleSelectionChange(val){

      },
      handleSizeChange(val) {
        this.tableData.pagination.pageSize = val;
        this.loadData();
      },
      handleCurrentChange(val) {
        this.tableData.pagination.pageNo = val;
        this.loadData();
      },
      gonext(row, val, cell, event){
        this.$router.push({path: '/tongji/cusAnswerDetail',query:{key1:this.searchKey[0],key2:this.searchKey[1],tel:row.tel}});
      },
      download(){
        tongjiApi.getCusAnswer({
          key: this.searchKey,
          type: 'excel',
      }).then( res => {
          if ( res.code == "200" || res.code == 200 ) {
              window.open("http://101.132.132.117:12580/"+res.data,"_self");
              //window.open("http://localhost:12580/"+res.data,"_self");
            }
        });
      },
      loadData(){
          tongjiApi.getUserChatNum({
            key: this.searchKey,
            timeType: this.cycle,
            demos: this.demos
          })
          .then(res => {
            this.myChart = this.$echarts.init(document.getElementById('myChart'));
            if ( this.demos == 0 ) {
                var dx = ['1次','2~4次','5~10次','11~20次','21~50次','50+次'];
                var option = ['呼通次数'];
                var series = {
                    name:'呼通次数',
                    type:'bar',
                    barWidth: 30,
                    data:[res.one, res.two, res.three, res.four, res.five, res.six]
                };
            }
            if ( this.demos == 1 ) {
                var dx = ['0~3秒','3~10秒','11~30秒','31~60秒','1~3分','3~10分','11~30分','31~60分','60+分'];
                var option = ['通话时长'];
                var series = {
                    name:'通话时长',
                    type:'bar',
                    barWidth: 30,
                    data:[res.one, res.two, res.three, res.four, res.five, res.six,res.seven,res.eight,res.nine]
                };
            }
            this.myChart.setOption({
                tooltip : {
                    trigger: 'axis',
                    axisPointer : {            
                        type : 'shadow'       
                    }
                },
                legend: {
                    data:option,
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis : [
                    {
                        type : 'category',
                        data : dx
                    }
                ],
                yAxis : [
                    {
                        type : 'value'
                    }
                ],
                series : [
                    series
                ]
            });

            var sk = this.searchKey;
            var cl = this.cycle;
            var dm = this.demos;
            var tb = this.tableData;
            //查看明细
            this.myChart.on('click', function (params) {
              console.log(params);
              let num = [];
              if ( params.seriesName == '呼通次数' ) {
                if ( params.dataIndex==0 ) {
                  num=[1];
                }
                if ( params.dataIndex==1 ) {
                  num = [2,4]
                }
                if ( params.dataIndex==2 ) {
                  num = [5,10]
                }
                if ( params.dataIndex==3 ) {
                  num=[11,20];
                }
                if ( params.dataIndex==4 ) {
                  num = [21,50];
                }
                if ( params.dataIndex==5 ) {
                  num=[50];
                }
              }
              if ( params.seriesName == '通话时长' ) {
                if ( params.dataIndex==0 ) {
                  num=[0,3];
                }
                if ( params.dataIndex==1 ) {
                  num = [3,10]
                }
                if ( params.dataIndex==2 ) {
                  num = [10,30]
                }
                if ( params.dataIndex==3 ) {
                  num=[30,60];
                }
                if ( params.dataIndex==4 ) {
                  num = [60,180];
                }
                if ( params.dataIndex==5 ) {
                  num=[180,600];
                }
                if ( params.dataIndex==6 ) {
                  num=[600,1800];
                }
                if ( params.dataIndex==7 ) {
                  num=[1800,3600];
                }
                if ( params.dataIndex==8 ) {
                  num=[3600];
                }
              }
              tongjiApi.getUserChatNumDetail({
                key: sk,
                timeType: cl,
                demos: dm,
                num: num
              })
              .then(res => {
                console.log
                tb.rows = res;
                tb.pagination.total = res.length;
              })
            })
          });
        }
    },
    created(){
      this.loadData();
    }
  }
</script>
<style>
  .el-pagination {
    float: right;
    margin-top: 15px;
  }
</style>



