<template>
  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-row>
        <el-col :span="24">
          <div class="chooseDate" style="float:right;">
            <el-date-picker
              v-model="searchKey"
              type="daterange"
              align="right"
              unlink-panels
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :picker-options="pickerOptions2"
              @change="search($event)">
            </el-date-picker>
          </div>
        </el-col>  
      </el-row>
    </h3>
    <div slot="body">
      <div id="myChart" :style="{width: '100%', height: '300px'}"></div>
      <div id="myChart1" :style="{width: '50%', height: '300px',float: 'left'}"></div>
      <div id="myChart2" :style="{width: '50%', height: '300px',float:'right'}"></div>
    </div>
  </imp-panel>
</template>

<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"
  import testData from "../../../static/data/data.json"
  import * as tongjiApi from '../../services/tongji'

  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        currentRow: {},
        dialogVisible: false,
        dialogLoading: false,
        defaultProps: {
          children: 'children',
          label: 'name',
          id: "id",
        },
        roleTree: [],
        listLoading: false,
        searchKey: [ new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 6), new Date()],
        tableData: {
          pagination: {
            total: 0,
            pageNo: 1,
            pageSize: 10,
            parentId: 0
          },
          rows: []
        },
        pickerOptions2: {
          shortcuts: [{
            text: '最近15天',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 14);
              picker.$emit('pick', [start, end]);
            }
          },{
            text: '最近30天',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 29);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
      }
    },
    methods: {
      search() {
        this.loadData();        
      },
      handleSelectionChange(val){

      },
      handleSizeChange(val) {
          this.tableData.pagination.pageSize = val;
          this.loadData();
      },
      handleCurrentChange(val) {
        this.tableData.pagination.pageNo = val;
        this.loadData();
      },
      loadData(){
          tongjiApi.countuser({
            key: this.searchKey,
            pageSize: this.tableData.pagination.pageSize,
            pageNo: this.tableData.pagination.pageNo
          })
          .then(res => {
            this.tableData.rows = res.records;
            this.tableData.pagination.total = res.total;
            //x轴数据
            let dx1 = this.$moment(this.searchKey[0]).format('YYYY-MM-DD');
            let dx2 = this.$moment(this.searchKey[1]).format('YYYY-MM-DD');
            let days = (this.searchKey[1]-this.searchKey[0])/(24*60*60*1000);
            let dx = [];
            let blind = [];
            let angel = [];
            let customer = [];
            let vo = [];
            for ( var i=0; i<=days ;i++ )  {
              let d = this.$moment(dx1).add(i, 'days').format('YYYY-MM-DD');
              dx.push(d);
            }
            for ( let index in res.records ) {
              let x = dx.indexOf(res.records[index].createdAt);
              if ( x != -1 ) {
                blind[x] = res.records[index].blindnum;
                angel[x] = res.records[index].friendnum;
                customer[x] = res.records[index].customernum;
                vo[x] = res.records[index].volunteernum
              }
            }
            console.log(blind);
            console.log(angel);
            console.log(customer);
            console.log(vo);
            //初始化
            let myChart = this.$echarts.init(document.getElementById('myChart'))
            // 绘制图表
            myChart.setOption({
              title: {
                text: '用户增涨表'
              },
              tooltip: {
                  trigger: 'axis'
              },
              legend: {
                  data:['视友数','亲友数','客服数','志愿者数']
              },
              grid: {
                  left: '3%',
                  right: '4%',
                  bottom: '3%',
                  containLabel: true
              },
              toolbox: {
                  feature: {
                      saveAsImage: {}
                  }
              },
              xAxis: {
                  type: 'category',
                  boundaryGap: false,
                  data: dx
              },
              yAxis: {
                  type: 'value'
              },
              series: [
                  {
                      name:'视友数',
                      type:'line',
                      stack: '总量',
                      data:blind
                  },
                  {
                      name:'亲友数',
                      type:'line',
                      stack: '总量',
                      data:angel
                  },
                  {
                      name:'客服数',
                      type:'line',
                      stack: '总量',
                      data:customer
                  },
                  {
                      name:'志愿者数',
                      type:'line',
                      stack: '总量',
                      data:vo
                  },
              ]
            });
          });
          tongjiApi.CountBlind2Friend({
            key: this.searchKey,
          })
          .then(res => {
            //初始化
            let myChart = this.$echarts.init(document.getElementById('myChart1'))
            // 绘制图表
            myChart.setOption({
                title: { text: '盲人绑定的亲友数分布', x:'center' },
                tooltip : {
                  trigger: 'item',
                  formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                toolbox: {
                  feature: {
                      saveAsImage: {}
                  }
                },
                legend: {
                      orient: 'vertical',
                      left: 'left',
                      data: ['未绑定亲友','绑定1个亲友','绑定2个亲友','绑定3个亲友','绑定4个亲友','绑定5个亲友','绑定6-10个亲友','绑定10个以上的亲友']
                },
                series : [
                  {
                    name: '访问来源',
                    type: 'pie',
                    radius : '55%',
                    center: ['50%', '60%'],
                    data:[
                        {value:res.records[0].zero, name:'未绑定亲友'},
                        {value:res.records[0].one, name:'绑定1个亲友'},
                        {value:res.records[0].two, name:'绑定2个亲友'},
                        {value:res.records[0].three, name:'绑定3个亲友'},
                        {value:res.records[0].four, name:'绑定4个亲友'},
                        {value:res.records[0].five, name:'绑定5个亲友'},
                        {value:res.records[0].sixtoten, name:'绑定6-10个亲友'},
                        {value:res.records[0].overten, name:'绑定10个以上亲友'},

                    ],
                    itemStyle: {
                        emphasis: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                }]
            });
          });
          tongjiApi.CountFriend2blind({
            key: this.searchKey,
          })
          .then(res => {
            this.tableData.rows = res.records;
            this.tableData.pagination.total = res.total;
            if ( parseInt(res.total) > 0 ) {
                           //初始化
            let myChart = this.$echarts.init(document.getElementById('myChart2'))
            // 绘制图表
            myChart.setOption({
                title: { text: '亲友绑定的盲人数分布', x:'center' },
                tooltip : {
                  trigger: 'item',
                  formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                toolbox: {
                  feature: {
                      saveAsImage: {}
                  }
                },
                legend: {
                      orient: 'vertical',
                      left: 'left',
                      data: ['未绑定盲人','绑定1个盲人','绑定2个盲人','绑定3个盲人','绑定4个盲人','绑定5个盲人','绑定6-10个盲人','绑定10个以上的盲人']
                },
                series : [
                  {
                    name: '访问来源',
                    type: 'pie',
                    radius : '55%',
                    center: ['50%', '60%'],
                    data:[
                        {value:res.records[0].zero, name:'未绑定盲人'},
                        {value:res.records[0].one, name:'绑定1个盲人'},
                        {value:res.records[0].two, name:'绑定2个盲人'},
                        {value:res.records[0].three, name:'绑定3个盲人'},
                        {value:res.records[0].four, name:'绑定4个盲人'},
                        {value:res.records[0].five, name:'绑定5个盲人'},
                        {value:res.records[0].sixtoten, name:'绑定6-10个盲人'},
                        {value:res.records[0].overten, name:'绑定10个以上盲人'},

                    ],
                    itemStyle: {
                        emphasis: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                }]
            });
            }

          });

      }
    },
    created(){
      this.loadData();
    }
  }
</script>
<style>
  .el-pagination {
    float: right;
    margin-top: 15px;
  }
</style>



