<template>
  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-row style="width: 100%;">
        <el-col :span="3">
          <el-button type="primary" icon="download" @click="download()">导出excel</el-button>
        </el-col>
        <el-col :span="16" style="text-align:left">
          <el-switch
              width= "100"
              v-model="userType"
              active-value="1"
              inactive-value="0"
              on-text="全部用户"
              off-text="真实用户"
              @change="reSearch()"
            >
            </el-switch>
          <el-date-picker
              v-model="searchKey" 
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              value-format="yyyy-MM-dd HH:mm:ss"
              @change="search($event)">
            </el-date-picker>
        </el-col>
      </el-row>
    </h3>
    <div slot="body">
      <el-table
        :data="tableData.rows"
        border
        style="width: 100%"
        v-loading="listLoading"
        @selection-change="handleSelectionChange">
        <!--checkbox 适当加宽，否则IE下面有省略号 https://github.com/ElemeFE/element/issues/1563-->
        <el-table-column
          prop="hangup_reason"
          type="selection"
          width="50">
        </el-table-column>
        <el-table-column
          prop="name"
          label="姓名" 
          >
        </el-table-column>
        <el-table-column
          prop="tel"
          label="电话号码" 
          >
        </el-table-column>
        <el-table-column
          prop="check_time"
          label="发起自检时间" 
          >
        </el-table-column>
        <el-table-column
          prop="check_start_time"
          label="开始自检时间" 
          >
        </el-table-column>
        <el-table-column
          prop="check_end_time"
          label="自检结束时间"
        >
        </el-table-column>
        <el-table-column
          prop="check_result"
          label="自检结果"
        >
        </el-table-column>
        <el-table-column
          prop="restore_method"
          label="修复方式"
        >
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="tableData.pagination.pageNo"
        :page-sizes="[5, 10, 20]"
        :page-size="tableData.pagination.pageSize"
        layout="total"
        :total="tableData.pagination.total">
      </el-pagination>
    </div>
  </imp-panel>
</template>

<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"
  import testData from "../../../static/data/data.json"
  import * as tongjiApi from '../../services/tongji'

  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        userType: Boolean(this.$route.query.userType),
        currentRow: {},
        dialogVisible: false,
        dialogLoading: false,
        defaultProps: {
          children: 'children',
          label: 'name',
          id: "id",
        },
        roleTree: [],
        listLoading: false,
        searchKey: (this.$route.query.timeType=='day' && typeof(this.$route.query.reason)=='undefined')?[this.$moment(this.$route.query.date).format('YYYY-MM-DD 00:00:00'),this.$moment(this.$route.query.date).add(1,'days').format('YYYY-MM-DD 00:00:00')]:(this.$route.query.timeType=='day'&&this.$route.query.reason!='undefined')?[this.$moment(this.$route.query.key1).format('YYYY-MM-DD 00:00:00'),this.$moment(this.$route.query.key2).format('YYYY-MM-DD 00:00:00')]:'',
        searchReason: '所有原因',
        tableData: {
          pagination: {
            total: 0,
            pageNo: 1,
            pageSize: 10,
            parentId: 0
          },
          rows: []
        }
      }
    },
    methods: {
      search(target){
        this.loadData();
      },
      reSearch() {
        this.loadData();
      },
      reCount() {
        this.loadData();
      },
      handleSelectionChange(val){
        this.searchReason='';
        for ( let index in val ) {
          this.searchReason += val[index].hangup_reason+';';
        }
      },
      dateFormat:function(time) {
          var date=new Date(time);
          var year=date.getFullYear();
          var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
          var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
          var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
          var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
          var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
          // 拼接
          return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
      },
      handleDetail(row) {
          if ( typeof(this.searchKey[0]) != 'undefined' && typeof(this.searchKey[0]) != 'undefined' ) {
            var key1 = this.dateFormat(this.searchKey[0]);
            var key2 = this.dateFormat(this.searchKey[1]);
          } else {
            var key1,key2;
          }
          this.$router.push({path: 'callFailReasonDetail', query: {hangup_reason: row.hangup_reason,total: row.callFailNum,key1:key1,key2:key2}})
      },
      handleSizeChange(val) {
        this.tableData.pagination.pageSize = val;
        this.loadData();
      },
      handleCurrentChange(val) {
        this.tableData.pagination.pageNo = val;
        this.loadData();
      },
      download(){
        tongjiApi.CountCallFailReasonExcel({
          searchKey: this.searchKey
        })
        .then( res => {
          if ( res.code == "200" || res.code == 200 ) {
              window.open("http://101.132.132.117:12580/"+res.data,"_self");
            }
        });
      },
      loadData(){
          tongjiApi.selfCheckDetail({
            key: this.$route.query.timeType=='day'?this.searchKey:this.$route.query.date,
            userType: this.userType,
            timeType: this.$route.query.timeType,
            type: this.$route.query.type,
            reason: this.$route.query.reason
          })
          .then(res => {
            this.tableData.rows = res;
            this.tableData.pagination.total = res.length;
          });
      }
    },
    created(){
      this.loadData();
    }
  }
</script>
<style>
  .el-pagination {
    float: right;
    margin-top: 15px;
  }
</style>



