<template>
  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-row style="width: 100%;">
        <el-col :span="3">
            <el-button type="primary" icon="download" @click="download()">导出excel</el-button>
        </el-col>
        <el-col :span="12">
            <el-date-picker
              v-model="searchKey" 
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              value-format="yyyy-MM-dd HH:mm:ss"
              @change="search($event)">
            </el-date-picker>
        </el-col>
      </el-row>
    </h3>
    <div slot="body">
      <el-table
        :data="tableData.rows"
        style="width: 100%"
        max-height="800"
        v-loading="listLoading"
        @selection-change="handleSelectionChange">
        <!--checkbox 适当加宽，否则IE下面有省略号 https://github.com/ElemeFE/element/issues/1563-->
        <el-table-column
          prop="id"
          type="selection"
          width="50">
        </el-table-column>
        <el-table-column
          prop="name"
          label="姓名" 
          >
        </el-table-column>
        <el-table-column
          prop="tel"
          label="手机号"
         >
        </el-table-column>
        <el-table-column
          prop="caller_tel"
          label="呼叫者手机号"
         >
        </el-table-column>
        <el-table-column
          prop="answer_time"
          label="接听时间"
        >
        </el-table-column>
        <el-table-column
          prop="start_time"
          label="通话开始时间"
        >
        </el-table-column>
        <el-table-column
          prop="end_time"
          label="通话结束时间"
         >
        </el-table-column>
        <el-table-column
          prop="hangup_time"
          label="挂断时间"
        >
        </el-table-column>
        <el-table-column
          prop="duration"
          label="通话时长"
        >
        </el-table-column>
        <el-table-column
          prop="hangup_reason"
          label="挂断原因"
        >
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="tableData.pagination.pageNo"
        :page-sizes="[5, 10, 20]"
        :page-size="tableData.pagination.pageSize"
        layout="total"
        :total="tableData.pagination.total">
      </el-pagination>
    </div>
  </imp-panel>
</template>

<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"
  import testData from "../../../static/data/data.json"
  import * as tongjiApi from '../../services/tongji'
  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        linkType: '',
        currentRow: {},
        dialogVisible: false,
        dialogLoading: false,
        defaultProps: {
          children: 'children',
          label: 'name',
          id: "id",
        },
        roleTree: [],
        listLoading: false,
        searchKey: [this.$moment(this.$route.query.key1).format('YYYY-MM-DD 00:00:00'),this.$moment(this.$route.query.key2).format('YYYY-MM-DD 00:00:00')],
        searchtel: this.$route.query.tel,
        tableData: {
          pagination: {
            total: 0,
            pageNo: 1,
            pageSize: 10,
            parentId: 0
          },
          rows: []
        }
      }
    },
    methods: {
      reSearch() {
        this.loadData();
      },
      search(target){
        this.loadData();
      },
      handleSelectionChange(val){

      },
      handleSizeChange(val) {
        this.tableData.pagination.pageSize = val;
        this.loadData();
      },
      handleCurrentChange(val) {
        this.tableData.pagination.pageNo = val;
        this.loadData();
      },
      download(){
        tongjiApi.getCusAnswerDetail({
          key: this.searchKey,
          tel: this.searchtel,
          type: 'excel',
      }).then( res => {
          if ( res.code == "200" || res.code == 200 ) {
              window.open("http://101.132.132.117:12580/"+res.data,"_self");
              //window.open("http://localhost:12580/"+res.data,"_self");
            }
        });
      },
      loadData(){
          tongjiApi.getCusAnswerDetail({
            key: this.searchKey,
            tel: this.searchtel,
          })
          .then(res => {
            this.tableData.rows = res;
            this.tableData.pagination.total = res.length;
          });
      }
    },
    created(){
      this.loadData();
    }
  }
</script>
<style>
  .el-pagination {
    float: right;
    margin-top: 15px;
  }
</style>



