<template>
  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-row style="width: 100%;">
        <el-col :span="3">
            <el-button type="primary" icon="download" @click="download()">导出excel</el-button>
        </el-col>
        <el-col :span="3">
          <el-switch
            width= "100"
            v-model="userType"
            active-value="1"
            inactive-value="0"
            on-text="全部用户"
            off-text="真实用户"
            @change="reSearch()"
          >
          </el-switch>
        </el-col>
        <el-col :span="6">
          <div class="el-input" style="width: 200px;">
            <i class="el-input__icon el-icon-search"></i>
            <input type="text" placeholder="输入用户手机号" v-model="searchtel" @keyup.enter="search($event)"
                   class="el-input__inner">
          </div>
        </el-col>
        <el-col :span="12">
            <el-date-picker
              v-model="searchKey" 
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              value-format="yyyy-MM-dd HH:mm:ss"
              @change="search($event)">
            </el-date-picker>
        </el-col>
      </el-row>
    </h3>
    <div slot="body">
      <el-table
        :data="tableData.rows"
        style="width: 100%;font-size:10px;"
        max-height="800"
        v-loading="listLoading"
        @selection-change="handleSelectionChange">
        <!--checkbox 适当加宽，否则IE下面有省略号 https://github.com/ElemeFE/element/issues/1563-->
        <el-table-column
          prop="id"
          type="selection"
          width="50">
        </el-table-column>
        <el-table-column
          fixed
          prop="name"
          label="姓名" 
          >
        </el-table-column>
        <el-table-column
          prop="tel"
          label="手机号"
         >
        </el-table-column>
        <el-table-column
          prop="callee_tel"
          label="接听者手机号"
         >
        </el-table-column>
        <el-table-column
          prop="check_time"
          label="发起自检时间"
         >
        </el-table-column>
        <el-table-column
          prop="check_start_time"
          label="开始自检时间"
         >
        </el-table-column>
        <el-table-column
          prop="check_end_time"
          label="自检结束时间"
         >
        </el-table-column>
        <el-table-column
          prop="check_result"
          label="自检结果"
         >
        </el-table-column>
        <el-table-column
          prop="restore_method"
          label="修复方式"
        >
        </el-table-column>
        <el-table-column
          prop="call_status"
          label="呼叫状态"
        >
        </el-table-column>
         <el-table-column
          prop="answer_status"
          label="接听状态"
         >
        </el-table-column>
         <el-table-column
          prop="chat_status"
          label="接通状态"
         >
        </el-table-column>
            <el-table-column
          prop="av_status"
          label="音视频状态"
         >
        </el-table-column>
        <el-table-column
          prop="hangup_reason"
          label="挂断原因"
         >
        </el-table-column>
        <el-table-column
          prop="call_time"
          label="发起呼叫时间"
        >
        </el-table-column>
         <el-table-column
          prop="answer_time"
          label="接听时间"
         >
        </el-table-column>
         <el-table-column
          prop="start_time"
          label="通话开始时间"
         >
        </el-table-column>
        </el-table-column>
         <el-table-column
          prop="end_time"
          label="通话结束时间"
         >
        </el-table-column>
        </el-table-column>
        <el-table-column
          prop="hangup_time"
          label="挂断时间"
         >
        </el-table-column>
        <el-table-column
          prop="duration"
          label="通话时长"
         >
        </el-table-column>
        <el-table-column
          prop="flowa"
          label="亲友端流量(kb)"
         >
        </el-table-column>
        <el-table-column
          prop="flowb"
          label="盲人端流量(kb)"
         >
        </el-table-column>
         <el-table-column
          prop="av_reason"
          label="音视频异常原因"
         >
        </el-table-column>
        <el-table-column
          prop="user_agent"
          label="视友端版本号"
         >
        </el-table-column>
        <el-table-column
          prop="ua"
          label="亲友端版本号"
        >
        </el-table-column>
        <el-table-column
          prop="chat_id"
          label="通话id"
        >
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="tableData.pagination.pageNo"
        :page-sizes="[5, 10, 20]"
        :page-size="tableData.pagination.pageSize"
        layout="total"
        :total="tableData.pagination.total">
      </el-pagination>
    </div>
  </imp-panel>
</template>

<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"
  import testData from "../../../static/data/data.json"
  import * as tongjiApi from '../../services/tongji'
  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        linkType: '',
        userType: Boolean(this.$route.query.userType),
        currentRow: {},
        dialogVisible: false,
        dialogLoading: false,
        defaultProps: {
          children: 'children',
          label: 'name',
          id: "id",
        },
        roleTree: [],
        listLoading: false,
        searchKey: [this.$moment(Date.now()).format('YYYY-MM-DD 00:00:00'),this.$moment(Date.now() + 24 * 60 * 60 * 1000).format('YYYY-MM-DD 00:00:00')],
        searchtel: '',
        tableData: {
          pagination: {
            total: 0,
            pageNo: 1,
            pageSize: 10,
            parentId: 0
          },
          rows: []
        }
      }
    },
    methods: {
      reSearch() {
        this.loadData();
      },
      search(target){
        this.loadData();
      },
      handleSelectionChange(val){

      },
    handleSizeChange(val) {
        this.tableData.pagination.pageSize = val;
        this.loadData();
      },
      handleCurrentChange(val) {
        this.tableData.pagination.pageNo = val;
        this.loadData();
      },
      download(){
        tongjiApi.getCallDetail({
          key: this.searchKey,
          tel: this.searchtel,
          userType: this.userType,
          type: 'excel',
      }).then( res => {
          if ( res.code == "200" || res.code == 200 ) {
              window.open("http://101.132.132.117:12580/"+res.data,"_self");
              //window.open("http://localhost:12580/"+res.data,"_self");
            }
        });
      },
      loadData(){
          tongjiApi.getCallDetail({
            key: this.searchKey,
            tel: this.searchtel,
            userType: this.userType,
            pageSize: this.tableData.pagination.pageSize,
            pageNo: this.tableData.pagination.pageNo
          })
          .then(res => {
            this.tableData.rows = res;
            this.tableData.pagination.total = res.length;
          });
      }
    },
    created(){
      this.loadData();
    }
  }
</script>
<style>
  .el-pagination {
    float: right;
    margin-top: 15px;
  }
</style>



